body{
    margin-top: 0px;
    padding: 0%;
    background-color: rgb(34, 26, 58);
}
.head{
    margin-top: 0px;
    padding: 0%;
    align-items: center;
    text-align: center;
    color: antiquewhite;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size: x-large;
}
h1{
    margin-top: 0px;
    padding: 10px;
    text-decoration: none;
}
.container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 30px;
  padding: 40px;
}

img{
    height: 50%;
    width: 50%;
    object-fit: contain;
    margin-top: 20px;
}
.p:hover ,.p1:hover{
    transform: translateY(-10px);
    transition: transform 0.5s ease-in-out;
    opacity: 70%;
}
.p1{
    color: azure;
    font-family: 'Comic Sans MS', 'Comic Sans', cursive;
    font-size: xx-large;
    padding-right: 0px;
    text-decoration: none;
    
}
a{
    text-decoration: none;
    color: inherit;
}
a:visited{
    text-decoration: none;
    color: inherit;
}
h2{
    color: whitesmoke;
    font-family: sans-serif;
}
.p3{
    font-size: x-large;
}
.p,.p1{
    background-color: rgb(36, 69, 107);
    align-items: center;
    text-align: center;
    justify-content: center;
    
}
